import React, { useEffect, useState } from "react";
import "./App.css";
import axios from "axios";
import {
  AppBar,
  Container,
  CssBaseline,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Typography
} from "@mui/material";
import Icon from '@mui/material/Icon';

// Components
import RPMRace from "./components/RPMRace/RPMRace";

const App = () => {
  const [racesData, setRacesData] = useState<any>(null);
  const [currentRace, setCurrentRace] = useState<any>(null);
  const [data, setData] = useState<any>(null);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenuClick = (sub: any) => {
    return () => {
      setAnchorEl(null);
      setCurrentRace(sub);
    }
  }

  useEffect(() => {
    axios.get('races.json').then((res) => setRacesData(res.data));
  }, []);

  useEffect(() => {
    if (!currentRace) return;
    axios.get('data_' + currentRace + '.json').then((res) => setData(res.data));
  }, [currentRace]);

  const getMenu = () => {
    if (!racesData) return;

    const menuItems: any[] = [];
    racesData.races.forEach((race: any) => {
      menuItems.push(<MenuItem onClick={handleMenuClick(race.subsession_id)}>{race.race_num} - {race.name}</MenuItem>);
    });
    return menuItems;
  };

  return (
    <div className="App">
      <CssBaseline />
      <AppBar position="static">
        <Toolbar>
          <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              sx={{ mr: 2 }}
              onClick={handleClick}
          >
            <Icon>menu</Icon>
          </IconButton>
          <Menu
              id="basic-menu"
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              MenuListProps={{
                'aria-labelledby': 'basic-button',
              }}
          >
            {getMenu()}
          </Menu>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            RPM European GT3 Series - Season 3
          </Typography>
        </Toolbar>
      </AppBar>
      <Container sx={{ height: "100%" }}>
        {data ? <RPMRace data={data}></RPMRace> : null}
      </Container>
    </div>
  );
};

export default App;
