import React, { FC, useEffect, useState } from "react";
import { Box, Tab, Tabs } from "@mui/material";

// Styles
import styles from "./RPMRace.module.css";

// Components
import RPMTabPanel from "../RPMTabPanel/RPMTabPanel";
import RPMResultsGrid from "../RPMResultsGrid/RPMResultsGrid";
import RPMLapsBars from "../RPMLapsBars/RPMLapsBars";
import RPMBoxPlot from "../RPMBoxPlot/RPMBoxPlot";
import RPMIncidents from "../RPMIncidents/RPMIncidents";

// Props
interface RPMRaceProps {
    data: any;
}

const RPMRace: FC<RPMRaceProps> = ({ data }) => {
    const [value, setValue] = React.useState(0);

    const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
        setValue(newValue);
    };

    const a11yProps = (index: number) => {
        return {
            id: `simple-tab-${index}`,
            "aria-controls": `simple-tabpanel-${index}`,
        };
    };

    return (
        <div className={styles.RPMRace} data-testid="RPMRace">
            <h2>
                {data.track_name}
            </h2>
            <Box sx={{ width: "100%", height: "100%" }}>
                <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
                    <Tabs
                        value={value}
                        onChange={handleTabChange}
                        aria-label="Race results tabs"
                    >
                        <Tab label="Results" {...a11yProps(0)} />
                        <Tab label="Qualy laps" {...a11yProps(1)} />
                        <Tab label="Race laps" {...a11yProps(2)} />
                        <Tab label="Lap times distribution" {...a11yProps(3)} />
                        <Tab label="Incidents" {...a11yProps(4)} />
                    </Tabs>
                </Box>
                <RPMTabPanel value={value} index={0}>
                    <RPMResultsGrid data={data.drivers_statistics} />
                </RPMTabPanel>
                <RPMTabPanel value={value} index={1}>
                    <RPMLapsBars
                        data={data.drivers_statistics}
                        lap_type={"best_qual_lap_time"}
                    />
                </RPMTabPanel>
                <RPMTabPanel value={value} index={2}>
                    <RPMLapsBars data={data.drivers_statistics} />
                </RPMTabPanel>
                <RPMTabPanel value={value} index={3}>
                    <RPMBoxPlot data={data.drivers_statistics} />
                </RPMTabPanel>
                <RPMTabPanel value={value} index={4}>
                    <RPMIncidents data={data.drivers_statistics} />
                </RPMTabPanel>
            </Box>
        </div>
    );
};

export default RPMRace;
