import React, { FC, useState } from "react";

import { AgGridReact } from "ag-grid-react";

// Styles
import styles from "./RPMResultsGrid.module.css";
import "ag-grid-community/dist/styles/ag-grid.css";
import "ag-grid-community/dist/styles/ag-theme-alpine.css";
import { laptimeToHumanReadable } from "../../helpers/laps";

// Props
interface RPMResultsGridProps {
  data: any[];
}

const RPMResultsGrid: FC<RPMResultsGridProps> = ({ data }) => {
  const [columnDefs] = useState([
    {
      field: "finish_position",
      type: 'numericColumn',
      headerName: "#",
      width: 30,
      pinned: 'left',
      valueGetter: (params: any) => {
        let pos = params.data.finish_position + 1;

        if (pos === 1) {
          pos = 'ðŸ†';
        }
        return pos;
    }},
    {
      field: "name (class #)",
      pinned: 'left',
      valueGetter: (params: any) => {
        let posCls = params.data.finish_position_in_class + 1;

        if (posCls === 1) {
          posCls = 'ðŸ†';
        }

        return `${params.data.name} (${posCls})`;
      },
    },
    {
      field: "best_lap",
      type: 'numericColumn',
      headerName: "Best lap",
      valueFormatter: (params: any) => {
        return laptimeToHumanReadable(params.value);
      },
    },
    {
      field: "best_qual_lap_time",
      type: 'numericColumn',
      headerName: "Qualy",
      valueFormatter: (params: any) => {
        return laptimeToHumanReadable(params.value);
      },
    },
    {
      field: "average_lap",
      type: 'numericColumn',
      headerName: "Avg lap",
      valueFormatter: (params: any) => {
        return laptimeToHumanReadable(params.value);
      },
    },
    { field: "incidents", type: 'numericColumn' },
    {
      headerName: "Positions gained",
      type: 'numericColumn',
      valueGetter: (params: any) =>
        params.data.starting_position - params.data.finish_position,
    },
    { field: "laps_complete", type: 'numericColumn', headerName: "Laps completed" },
    { field: "class", filter: true },
  ]);

  return (
    <div className={styles.RPMResultsGrid}>
      <div className="ag-theme-alpine" style={{ height: 600, width: "100%" }}>
        <AgGridReact rowData={data} columnDefs={columnDefs}></AgGridReact>
      </div>
    </div>
  );
};

export default RPMResultsGrid;
